$baseSize: 40px;
$secondSize: ($baseSize / 2);

.fm-loader {
  position: relative;
  width: 100%;
  height: $baseSize;
  top: calc(50% - #{$secondSize});
  text-align: center;
  margin: 0 auto;
}

.fm-loader span {
    position: absolute;
    border: 3px solid #2EA1F8;
    border-top: 3px solid transparent;
    border-radius: 999px;
    &:nth-child(1) {
        width: $baseSize;
        height: $baseSize;
        left: calc(50% - #{$secondSize});
        -webkit-animation: spin-1 1s infinite ease;
        -moz-animation: spin-1 1s infinite ease;
        animation: spin-1 1s infinite ease;
        border-color: #2EA1F8;
        border-top-color: transparent;
    }
    &:nth-child(2) {
    $halfSecondSize: ($secondSize / 2);
    top: $halfSecondSize;
    left: calc(50% - #{$halfSecondSize});
    width: $secondSize;
    height: $secondSize;
    -webkit-animation: spin-2 1s infinite ease;
    -moz-animation: spin-2 1s infinite ease;
    animation: spin-2 1s infinite ease;
    }
}

@-webkit-keyframes spin-1 {
  0% {
    -webkit-transform: rotate(360deg);
    opacity: 1;
  }
  50% {
    -webkit-transform: rotate(180deg);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: rotate(0deg);
    opacity: 1;
  }
}

@-moz-keyframes spin-1 {
  0% {
    -moz-transform: rotate(360deg);
    opacity: 1;
  }
  50% {
    -moz-transform: rotate(180deg);
    opacity: 0.5;
  }
  100% {
    -moz-transform: rotate(0deg);
    opacity: 1;
  }
}

@keyframes spin-1 {
  0% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
    opacity: 1;
  }
  50% {
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    transform: rotate(180deg);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    opacity: 1;
  }
}

@-webkit-keyframes spin-2 {
  0% {
    -webkit-transform: rotate(0deg);
    opacity: 0.5;
  }
  50% {
    -webkit-transform: rotate(180deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: rotate(360deg);
    opacity: 0.5;
  }
}

@-moz-keyframes spin-2 {
  0% {
    -moz-transform: rotate(0deg);
    opacity: 0.5;
  }
  50% {
    -moz-transform: rotate(180deg);
    opacity: 1;
  }
  100% {
    -moz-transform: rotate(360deg);
    opacity: 0.5;
  }
}

@keyframes spin-2 {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    opacity: 0.5;
  }
  50% {
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    transform: rotate(180deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
    opacity: 0.5;
  }
}