/* -----
Variable
----- */
$color-primary: #2EA1F8;
$color-grey: #eff3f6;
$color-dark: #354052;
$color-success: #39B54A;
$color-error: #ec2415;

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
}
body {
  margin: 0;
  overflow-x: hidden;
}

* {
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
}
*:before,
*:after {
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
}
html {
  font-size: 10px;

  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 1.42857143;
  color: #333;
  background-color: #fff;
}

a {
	text-decoration: none;
}

ol {
	margin-top: 0;
}
/* -----
SVG Icons - svgicons.sparkk.fr
----- */

.svg-icon {
  width: 1em;
  height: 1em;
}

.svg-icon path,
.svg-icon polygon,
.svg-icon rect {
  fill: $color-dark;
}

.svg-icon circle {
  stroke: $color-dark;
  stroke-width: 1;
}

/* --------------------------------------------- */

.file-manager {
	width: 100%;
	//max-width: 900px;
	margin: auto;
	min-height: 100%;
	position: relative;
	background: $color-grey;
	padding: 30px 15px;
	padding-top: 95px;
}

@import 'preloader';

.fm-loader-wrapper {
	width: 100%;
	min-height: 200px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.fm-empty {
	text-align: center;
	font-size: 16px;
	color: $color-dark;
}

// Navigation
.fm-nav {
	position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
}
.fm-nav__body {
	width: 100%;
	margin: auto;
	background: $color-dark;
	color: #fff;
}
.fm-nav__top {
	padding: 5px 15px;
	background: darken($color-dark, 10%);
}
.fm-nav__bottom {
	padding: 8px 15px;
}

// breadcrumbs
.fm-breadcrumb {
	display: flex;
	align-items: center;
	list-style: none;
	padding: 0 10px;
	margin-bottom: 0;
}
.fm-breadcrumb__item {
	margin-right: 5px;
	&:after {
		content: '→';
		margin-left: 3px;
		color: mix($color-grey, $color-dark, 30%);
	}
	&:last-child {
		&:after {
			display: none;
		}
	}
}
.fm-breadcrumb__link {
	color: $color-primary;
	&:hover, &:focus {
		outline: none;
		text-decoration: none;
		color: lighten($color-primary, 5%);
	}
}

// File Input
@import 'progress-button';

.fm-input {
	margin-left: 10px;
	label {
		margin-bottom: 0;
		background: $color-primary;
		color: #fff;
		padding: 5px 10px;
		display: inline-block;
		border-radius: 3px;
		margin-left: 10px;
		margin-bottom: 0;
		position: relative;
		min-width: 100px;
		text-align: center;
		text-transform: uppercase;
		font-size: 12px;
	}
	input {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		opacity: 0;
		cursor: pointer;
		&::-webkit-file-upload-button {
			cursor: pointer;
		}
	}

	.svg-icon {
		font-size: 16px;
		margin-right: 5px;
		path, polygon, rect {
			fill: darken($color-primary, 25%);
		}
	}
}

// files
.files {
	display: flex;
	flex-wrap: wrap;
}
.file {
	flex-basis: (100% / 4);
	max-width: (100% / 4);
	padding: 10px;
	font-family: 'Lato', sans-serif;
}
.file__body {
	display: block;
	background: #fff;
    border-radius: 3px;
    padding: 5px;
    border: 1px solid #e6eaee;
    color: $color-dark;

    &:hover, &:focus {
		text-decoration: none;
		outline: none;
	}
}
.file--selected {
	background: $color-primary;
	color: #fff;
	&:hover, &:focus {
		color: #fff;
	}

	.file__size {
		color: $color-grey;
	}
}
%file__thumb {
	padding-top: 75%;
	background: #f5f8fa;
	border-radius: 3px;
	position: relative;
	overflow: hidden;
	img {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		margin: auto;
	}
}
.file__image {
	@extend %file__thumb;
	img {
		width: 100%;
	}
}
.file__icon {
	@extend %file__thumb;
	img {
		width: 50px;
	}
}
.file__meta {
	padding: 8px 7px 3px;
}
.file__name {
	display: flex;
	align-items: center;
	margin-bottom: 5px;
	img {
		margin-right: 10px;
	}
    span {
    	white-space: nowrap;
	    text-overflow: ellipsis;
	    overflow: hidden;
	    display: block;
    }
}
.file__size {
	font-size: 12px;
	color: darken($color-grey, 25%);
}

// folders
.folders {
	@extend .files;
}
.folder {
	@extend .file;
}
.folder__body {
	@extend .file__body;
	padding: 8px 12px;
	display: flex;
	align-items: center;
}
.folder--selected {
	@extend .file--selected;

	.folder__icon {
		&:before {
			background-color: darken($color-grey, 10%);
			box-shadow: 21px 4px 0 0 darken($color-grey, 10%);
		}
		&:after {
			background-color: $color-grey;
		}
	}

	.folder__details {
		color: $color-grey;
	}
}

.folder__icon {
	font-size: 23px;
	display: inline-block;
	background-color: transparent;
	overflow: hidden;
	margin-right: 10px;
	flex-shrink: 0;
	&:before {
		content: '';
		float: left;
		background-color: darken($color-primary, 15%);

		width: 25px;
		height: 8px;

		margin-left: 1px;
		margin-bottom: -2px;

		border-top-left-radius: 2px;
		border-top-right-radius: 2px;

		box-shadow: 21px 4px 0 0 darken($color-primary, 15%);
	}
	&:after {
		content: '';
		float: left;
		clear: left;

		background-color: $color-primary;
		width: 50px;
		height: 35px;

		border-radius: 0.1em;
	}
}

.folder__icon--not-empty:before {
	height: 10px;
}
.folder__icon--not-empty:after {
	height: 33px;
	box-shadow: 0 -2px 0 0 #fff;
}

.folder__meta {
	max-width: calc(100% - 60px);
}

.folder__name {
	@extend .file__name;
	margin-bottom: 0;
	display: block;
}
.folder__details {
	font-size: 12px;
	color: darken($color-grey, 25%);
}

// Context Menu
.context-menu {
	display: none;
	position: absolute;
	z-index: 10;
	padding: 8px 0;
	width: 240px;
	background-color: #fff;
	border: 1px solid #e6eaee;
	border-radius: 3px;
	box-shadow: 0 0 20px 0 rgba(0,0,0,.2);
}

.context-menu--active {
	display: block;
}

.context-menu__items {
	list-style: none;
	margin: 0;
	padding: 0;
}

.context-menu__item {
	display: block;
}

.context-menu__divider {
	height: 1px;
	width: 100%;
	display: block;
	background: #ccc;
	margin: 8px 0;
}

.context-menu__link {
	display: flex;
    // align-items: center;
	padding: 8px 15px;
	color: $color-dark;
	text-decoration: none;
	font-size: 12px;
	.svg-icon {
		font-size: 16px;
		min-width: 20px;
		margin-right: 10px;
	}
}

.context-menu__link:hover {
	color: #fff;
	background-color: $color-primary;
	text-decoration: none;
	.svg-icon path, .svg-icon polygon, .svg-icon rect {
		fill: #fff;
	}
}

// Notification
@import 'notification';
@import 'modal';