%fixed-position {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin: auto;
}
.finder-modal__overlay {
	@extend %fixed-position;
	background: rgba(#212833, .8);
	z-index: 100;
}

.finder-modal__main {
	@extend %fixed-position;
	background: #EFF3F6;
	top: 5%;
	bottom: 5%;
	z-index: 101;
	overflow: hidden;
	iframe {
		width: 100%;
		height: 100%;
		border: 0;
	}
}

.finder-modal__footer {
	position: absolute;
	bottom: 0;
	padding: 8px 15px;
	background: #fff;
	width: 100%;
	height: 60px;
    border-top: 1px solid #e6eaee;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    button {
    	background: #fff;
    	border: 1px solid darken(#e6eaee, 10%);
    	border-radius: 3px;
    	padding: 6px 12px;
    	margin-left: 15px;
    	&:hover, &:focus {
    		background: #e6eaee;
    		outline: none;
    	}

    	&.finder-modal__select {
			background: $color-primary;
			border-color: darken($color-primary, 10%);
			color: #fff;
			&:hover, &:focus {
				background: darken($color-primary, 10%);
			}

			&:disabled {
				opacity: .5;
				-webkit-touch-callout: none;
				-webkit-user-select: none;
				-khtml-user-select: none;
				-moz-user-select: none;
				-ms-user-select: none;
				user-select: none;
				cursor: not-allowed;
				&:hover {
					background: $color-primary;
				}
			}
		}
    }
}

@media(min-width: 661px) {
	.finder-modal__main {
		width: 90%;
	}
}

@media(min-width: 961px) {
	.finder-modal__main {
		width: 75%;
	}
}

