/* Button container */
.fm-input {
	position: relative;
	display: inline-block;
	text-align: center;

	/* Button style */
	button {
		display: inline-block;
		margin: 0 auto;
		padding: 3px 10px;
		width: 120px;
		height: 30px;
		border: 2px solid transparent;
		border-radius: 3px;
		background: $color-primary;
		color: #fff;
		letter-spacing: 1px;
		font-size: 12px;
		text-transform: uppercase;
		-webkit-tap-highlight-color: transparent;
		transition: background-color 0.3s, color 0.3s, width 0.3s, border-width 0.3s, border-color 0.3s, border-radius 0.3s;

		&:hover {
			background-color: darken($color-primary, 5%);
			color: #fff;
		}

		&:focus {
			outline: none;
		}

		input {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			opacity: 0;
			cursor: pointer;
			&::-webkit-file-upload-button {
				cursor: pointer;
			}
		}

		/* Text (transition for when returning to initial state) */
		span {
			transition: opacity 0.3s 0.1s;
		}
	}

	/* Common style of SVGs */
	svg {
		position: absolute;
		top: 0;
		left: 50%;
		-webkit-transform: translateX(-50%);
		transform: translateX(-50%);
		pointer-events: none;

		path {
			opacity: 0;
			fill: none;
		}

		&.progress-circle path {
			stroke: $color-primary;
			stroke-width: 5;
		}

		&.checkmark path,
		&.cross path {
			stroke: #fff;
			stroke-linecap: round;
			stroke-width: 4;
			-webkit-transition: opacity 0.1s;
			transition: opacity 0.1s;
		}

		&.fm-input__icon {
			font-size: 16px;
			margin-right: 5px;
			position: relative;
			left: 0;
			transform: none;
			width: 1em;
		    height: 1em;
			path, polygon, rect {
				opacity: 1;
				fill: darken($color-primary, 25%);
			}
		}
	}
}

/* Loading, success and error effects */
.loading.fm-input {
	button {
		width: 30px; /* make a circle */
		border-width: 2px;
		border-color: #ddd;
		background-color: transparent;
		color: #fff;
		border-radius: 50px;
	}

	span {
		transition: opacity 0.15s;
	}
}

.loading.fm-input span,
.success.fm-input span,
.error.fm-input span {
	opacity: 0; /* keep it hidden in all states */
}

.success.fm-input button,
.error.fm-input button {
	transition: background-color 0.3s, width 0.3s, border-width 0.3s, border-radius 0.3s;
}

.success.fm-input button {
	border-color: #1ECD97;
	background-color: #1ECD97;
}

.error.fm-input button {
	border-color: #FB797E;
	background-color: #FB797E;
}

.loading.fm-input svg.progress-circle path,
.success.fm-input svg.checkmark path,
.error.fm-input svg.cross path {
	opacity: 1;
	transition: stroke-dashoffset 0.3s;
}
